import React from "react";
import PropTypes from "prop-types";
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
function reload(e) {
    e.target.disabled = true;
    let g = function (a) {
        return document.getElementById(a);
    };
    let tere = g("tere");
    let h1 = g("h1");
    tere.style.opacity = 0.3;
    tere.innerHTML = "<p>MOHON MENUNGGU...</p>";
    h1.innerHTML = "RESTARTING";
    window.location.reload();
}
function Result(props) {
    let benar = props.quizBenar;
    let salah = props.quizSalah;
    let nilai = props.quizNilai;
    let total = props.quizTotal;
    let persen_benar = nilai + "%";
    let persen_salah = (100 - nilai) + "%";
    
	return (
		<ReactCSSTransitionGroup
			className="container result"
			component ="div"
			transitionName="fade"
			transitionEnterTimeout={500}
			transitionLeaveTimeout={300}
			transitionAppear
			transitionAppearTimeout={300}
		>
			<div id="tere" className="tere">
                <h3>Hasil Test</h3>
                <hr />
                <p>Benar: <strong>{benar}</strong> ({persen_benar})</p>
                <p>Salah: <strong>{salah}</strong> ({persen_salah})</p>
                <p>Total soal: <strong>{total}</strong></p>
                <hr />
                <p>Nilai:</p>
                <div className="nilai">{nilai}</div>
                <p>
                    <button onClick={reload}>ULANGI TEST</button>
                </p>
			</div>
		</ReactCSSTransitionGroup>
	);
}
 
Result.propTypes = {
	quizBenar: PropTypes.number.isRequired,
    quizSalah: PropTypes.number.isRequired,
    quizNilai: PropTypes.number.isRequired,
    quizTotal: PropTypes.number.isRequired
};
 
export default Result;	


